import React, { Component } from 'react';
import $ from 'jquery';
window.api_url_base = '';

class App extends Component {

  state =
  {
    real_states: [],
  }

  componentDidMount() {
    this.serverRequest = this.serverRequest || [];
    this.serverRequest.push($.ajax({
      url: window.api_url_base + "/real_states.json",
      success: function (items) {
        var trs = [];

        for(var i = 0; i< items.length;i++)
        {
          trs.push(
              <tr key={i}>
                <td>{items[i].id}</td>
                <td>{items[i].name}</td>
                <td>{items[i].real_state_type}</td>
                <td>{items[i].city}</td>
                <td>{items[i].country}</td>
              </tr>
          )
        }

        this.setState({
          real_states: trs
        });
      }.bind(this),
      async: false
    }));
}

  componentWillUnmount() {
    for(var i=0;i<this.serverRequest.length;i++)
    {
      this.serverRequest[i].abort();
    }
}

render() {

  return (
    <div className="row">
      <div className="col-md-10 col-md-offset-1 col-xs-10 col-xs-offset-1">
        <div className="row"><h1>Real State Management (React Frontend)</h1></div>
        <table className="table table-striped table-bordered table-hover">
          <thead>
            <tr>
              <th>Id</th>
              <th>Name</th>
              <th>Type</th>
              <th>City</th>
              <th>Country</th>
            </tr>
          </thead>
          <tbody>
            {this.state.real_states}
          </tbody>
        </table>
      </div>
    </div>
  );
 }
}

export default App;
